import domReady from "@/js/modules/dom-ready";
import 'promise-polyfill/src/polyfill';
import store from '@/components/demo/demo.store.js';
import runtime from 'sapper/runtime.js';
import Router from '@/components/demo/demo.routes.html';

var application = /*Babel»*/void 0;

domReady(function () {
    if (process.env.NODE_ENV !== 'production') {
        import('./dev/keyboard-shortcuts.js').then(function (keyboardSC) /*Babel»*/{
            return keyboardSC.init();
        });
    }
    var appElement = document.getElementById('app');

    if (appElement) {
        var currentRoute = document.getElementById('currentRoute').innerHTML.toLowerCase();

        import(
        /* "webpackChunkName": "routes" */
        '@/components/' + currentRoute + '/' + currentRoute + '.demo.html').then(function (App) {

            new Router({
                hydrate: true,
                target: appElement,
                data: {
                    innerComponent: App["default"]
                }
            });

            // console.log(currentRoute)
            //
            // application =
            //     new App["default"]({
            //         target: appElement,
            //         hydrate: true,
            //         data: {
            //             thisPath: window.location.pathname
            //         }
            //     });

            window.__APP__ = application;

            return application;
        }, function (err) /*Babel»*/{
            return console.log(err);
        });
    }
});

window.store = store;

var cache = require.cache;
export { __webpack_modules__, __webpack_require__, cache, application }; // eslint-disable-line